<template>
    <div class="luck-draw-page">
        <!-- 头 -->
        <van-nav-bar title="" left-arrow :border="false" @click-left="goBack"/>
        <!-- 玩法规则 -->
		<img class="go-rule" src="../assets/imgs/go-rule-draw.png" alt="" @click="goRulePage">
        <div class="space"></div>
        <!-- 抽奖 -->
        <div class="wheel-main">
            <div class="wheel-pointer-box">
                    <div class="wheel-pointer" @click="rotate_handle()" :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
            </div>               
            <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">                   
                <div class="prize-list">
                    <div class="prize-item" v-for="(item,index) in prizeList" :key="index">
                        
                        <div class="prize-name">
                            {{item.prize}}
                        </div>

                        <div class="prize-pic">
                            <img :src="item.prize_img">
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <!-- 我的蚪金 -->
        <div class="money">我的蚪金：{{user_info.dw_money}}</div>
        <!-- 我的战绩 -->
        <div class="my-record">
            <img src="../assets/imgs/my-record.png" alt="" @click="goMore">
        </div>
        <!-- 中奖用户 -->
        <div class="win-box">
            <div class="win-content">
                <!-- 标题 -->
                <div class="win-title">
                    <div class="win-title-line"></div>
                    <div class="win-title-text">中奖用户</div>
                    <div class="win-title-line"></div>
                </div>
                <!-- 列表 -->
                <div class="win-list">
                    <div class="win-list-item win-list-item-title">
                        <div>中奖时间</div>
                        <div>获奖人</div>
                        <div>奖品名称</div>
                    </div>
                    <div class="win-list-item" v-for="(item,index) in winList" :key="index">
                        <div>{{item.add_time | timeFormat}}</div>
                        <div>{{item.user_name | userNameFormat}}</div>
                        <div>{{item.prize}}</div>
                    </div>
                </div>
                
            </div>
        </div>


        <!-- 抽奖提示 -->
		<van-popup v-model="showDrawNoticePopup">
			<div class="guess-box">
				<img src="../assets/imgs/draw-notice.png" alt="">
				<div class="guess-content">
					<div class="guess-content-text">您正在进行幸运转盘抽奖需消耗20蚪金，是否确认操作？</div>
                    <div class="tip-box">
                        <van-checkbox v-model="noTip">不再提示</van-checkbox>
                    </div>
					<div class="guess-btn-box">
						<img src="../assets/imgs/draw-notice-cancal.png" alt="" @click="closeDrawNotice">
						<img src="../assets/imgs/draw-notice-confirm.png" alt="" @click="showPayPassword">
					</div>
					
				</div>
			</div>
		</van-popup>

		<!-- 验证支付密码 -->
		<div class="popup" v-if="showPayPasswordPopup">
			<!-- 阴影 -->
			<div class="popup-shadow" @click="closePayPassword"></div>
			
			<div class="popup-password-input">
				<div class="popup-password-input-title">
					<van-icon name="cross" @click="closePayPassword"/>
					<span>输入支付密码，以验证身份</span>
				</div>
				<!-- 密码框 -->
				<van-password-input :value="payPw"/>
				<!-- 数字键盘 -->
				<van-number-keyboard :show="true" :hide-on-click-outside="false" @input="onInput" @delete="onDelete" />
			</div>
		</div>


    </div>
</template>



<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

export default {
    data(){
        return{
            winList:[],//中奖列表
            prizeList:[],//奖品列表
            start_rotating_degree: 0, //初始旋转角度
            rotate_angle: 0, //将要旋转的角度
            start_rotating_degree_pointer: 0, //指针初始旋转角度
            rotate_angle_pointer: 0, //指针将要旋转的度数
            rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
            rotate_transition_pointer: "transform 12s ease-in-out", //初始化指针过度属性控制
            click_flag: true, //是否可以旋转抽奖
            index: 0 ,

            showDrawNoticePopup:false,//抽奖提示
            noTip:false,
            showPayPasswordPopup:false,
			payPw:'',
            
        }
    },
    computed:{
        ...mapState(['user_info'])
    },
    filters: {
        //格式化中奖时间
        timeFormat(val) {
            
            let timestamp = parseInt(val) * 1000;
            let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
            let year = date.getFullYear();
            let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
            let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
            let hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
            let minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
            let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
            return hour + ":" + minute + ":" + seconds;
        },
        //格式化中奖用户名
        userNameFormat(val){
            let user_name = val + '';
            if(user_name.length >= 3){
                let start = user_name.substring(0,1);
                let end = user_name.substring(user_name.length - 1,user_name.length);
                return start + '**' + end;
            }else{

            }
        }
    },
    created(){
        this.utils.info('user_info...',this.user_info);
        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
			this.user_info.user_id = this.$route.params.id;
		}else{
			this.utils.info('没有id...未登录...');
			this.user_info.user_id = 1;
        }
        this.utils.info('user_info...',this.user_info);

        //获取用户信息
		this.getUserInfo('1');
    },
    mounted(){

    },
    methods:{
        ...mapMutations(['changeUserInfo']),
        //返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
        },
        //玩法规则
		goRulePage(){
			this.$router.push({
				name:'luckDrawRule'
			})
		},
        //我的战绩
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
        },
        //获取用户信息 type 1 去请求奖品列表和用户列表 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
                    this.user_info.token = data.token;

                    if(type == '1'){
                        //获取奖品列表
                        this.getPrizeList();

                        //获取中奖用户列表
                        this.getWinList();
                    }
                    
                }
			})
        },
        //获取奖品列表
        getPrizeList(){
            let option = {
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'turntable/prize',option).then(result =>{
                this.utils.info('获取奖品列表...',result); 
                if(result.success){
                    this.prizeList = result.data;
                }
            })
        },
        //获取中奖用户列表
        getWinList(){
            let option = {
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'turntable/record',option).then(result =>{
                this.utils.info('获取中奖用户列表...',result); 
                if(result.success){
                    this.winList = result.data;
                }
            })
        },
        rotate_handle() {
            if(this.noTip){
                this.showPayPassword();
            }else{
                this.showDrawNotice();
            }
            // this.index = 1 //指定每次旋转到的奖品下标
            // this.rotating();
        },
        rotating() {
            if (!this.click_flag) return;
            var type = 0; // 默认为 0  转盘转动 1 箭头和转盘都转动(暂且遗留)
            var during_time = 5; // 默认为1s
            var random = Math.floor(Math.random() * 7);
            var result_index = this.index ; // 最终要旋转到哪一块，对应prize_list的下标
            var result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //最终会旋转到下标的位置所需要的度数
            var rand_circle = 6; // 附加多转几圈，2-3
            this.click_flag = false; // 旋转结束前，不允许再次触发
            if (type == 0) {
                // 转动盘子
                var rotate_angle =
                this.start_rotating_degree +
                rand_circle * 360 +
                result_angle[result_index] -
                this.start_rotating_degree % 360;
                this.start_rotating_degree = rotate_angle;
                this.rotate_angle = "rotate(" + rotate_angle + "deg)";
                // // //转动指针
                // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
                // this.start_rotating_degree_pointer =360*rand_circle;
                var that = this;
                // 旋转结束后，允许再次触发
                setTimeout(function() {
                    that.click_flag = true;
                    that.game_over();
                }, during_time * 1000 + 1500); // 延时，保证转盘转完
            } else {
                //
            }
        },
        game_over() {
            this.toast_control = true;
        },
        //显示抽奖提示
        showDrawNotice(){
            this.showDrawNoticePopup = true;
        },
        //关闭抽奖提示
        closeDrawNotice(){
            this.showDrawNoticePopup = false;
        },
        //显示验证密码
		showPayPassword(){
            this.showDrawNoticePopup = false;
			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

			this.utils.showToast('您放弃了支付');
		},
		// 数字键盘 事件
		onInput(key) {
			
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				
                let option = {
                    pay_password: this.payPw,
                    token: this.user_info.token
                }
                this.utils.showLoading();
                this.http.post(this.http._HOST + 'turntable/draw',option).then(result =>{
                    this.utils.info('验证密码...',result); 
                    this.utils.dismissLoading();
                    if(result.success){
                        this.getUserInfo('2');
                        this.closePayPassword();
                    }else{
                        this.utils.showAlert(result.message);
                    }
                })
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
    }
    
}
</script>



<style scoped>
    .luck-draw-page{
		position: relative;
		background: url(../assets/imgs/luck-draw-bg.png);
		background-repeat: no-repeat;
		background-size: cover;
	}
	.van-nav-bar{
		background: transparent;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#fff;
	}
    
    /* 玩法规则 */
	.go-rule{
		position: absolute;
		width:85px;
		right:0;
		top:100px;
	}

    /* 占位 */
    .space{
        padding-bottom:240px;
    }

    /* 圆盘抽奖 */
    .wheel-main {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    .wheel-bg {
        width: 300px;
        height: 300px;
        background: url("../assets/imgs/draw_wheel.png") no-repeat center top;
        background-size: 100%;
        color: #fff;
        font-weight: 500;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        transition: transform 3s ease;
    }
    .wheel-pointer-box {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 5;
        transform: translate(-50%, -60%);
        width: 80px;
        height: 80px;
    }
    .wheel-pointer {
        width: 100%;
        height: 100%;
        background: url("../assets/imgs/draw_btn.png") no-repeat center top;
        background-size: 100%;
        transform-origin: center 60%;
    }
    .wheel-bg div {
        text-align: center;
    }
    .prize-list {
        width: 100%;
        height: 100%;
        position: relative;
    }
    .prize-list .prize-item {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        width: 94px;
        height: 144px;
        padding-top:30px;
    }
    .prize-list .prize-item:first-child {
        top: 0;
        left: 133px;
        transform: rotate(20deg);
    }
    .prize-list .prize-item:nth-child(2) {
        top: 45px;
        left: 172px;
        transform: rotate(67deg);
    }
    .prize-list .prize-item:nth-child(3) {
        top: 102px;
        left: 170px;
        transform: rotate(-250deg);
    }
    .prize-list .prize-item:nth-child(4) {
        top: 144px;
        left: 131px;
        transform: rotate(-210deg);
    }
    .prize-list .prize-item:nth-child(5) {
        top: 147px;
        left: 70px;
        transform: rotate(-160deg);
    }
    .prize-list .prize-item:nth-child(6) {
        top: 102px;
        left: 30px;
        transform: rotate(-111deg);
    }
    .prize-list .prize-item:nth-child(7) {
        top: 44.8px;
        left: 29px;
        transform: rotate(-69deg);
    }
    .prize-list .prize-item:nth-child(8) {
        top: 0;
        left: 72px;
        transform: rotate(-20deg);
    }
    
    .prize-name {
        font-size: 12px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding:0 10px 10px;
    }
    .prize-pic img {
        width: 30px;
        height: 30px;
    }
    

    /* 我的蚪金 */
    .money{
        text-align: center;
        color:#fff;
        font-size:16px;
        padding-bottom: 40px;
        padding-top:25px;
    }

    /* 我的战绩 */
    .my-record{
        text-align: center;
    }
    .my-record img{
        display: inline-block;
        width:266px;
    }

    /* 中奖用户 */
    .win-box{
        padding:30px 15px 40px;
    }
    .win-content{
        background: linear-gradient(to bottom, #27219E  , #1B52C4);
        padding:30px 20px;
        border-radius: 5px;
    }
    .win-title{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom:25px;
    }
    .win-title-text{
        font-size: 20px;
        font-weight: bold;
        color:#fff;
        padding:0 20px;
    }
    .win-title-line{
        width:90px;
        border-bottom: 1Px solid #fff;
    }
    .win-list-item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size:14px;
        color:#fff;
        padding-bottom: 20px;
    }
    .win-list-item:last-child{
        padding-bottom: 0;
    }
    .win-list-item div{
        width:30%;
        text-align: center;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .win-list-item-title{
        font-size:16px;
        padding-bottom: 30px;
    }


    /* 弹框 */
	.van-popup{
		width:84%;
		background: transparent;
	}
	
	.guess-box{
		position: relative;
	}
	.guess-box img{
		display: block;
		width:100%;
	}
	.close-guess{
		position: absolute;
		right:20px;
		top:40px;
		font-size:18px;
		color:#fff;
		z-index:2999;
	}
	.guess-content{
		position: absolute;
		width:100%;
		top:0;
		left:0;
		padding: 25px 25px 0;
		color: #fff;
	}

    .guess-content-text{
        font-size:18px;
        line-height: 30px;
        padding-top:170px;
    }
    .tip-box{
        padding:35px 0;
    }
    .van-checkbox__label{
        color:#fff;
    }
    .guess-btn-box{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .guess-btn-box img{
        width:130px;
    }
	




    /* 输入密码 */
	.popup {
		position: fixed;
		width:100%;
		height:100%;
		top: 0;
		left: 0;
		z-index: 10;
	}
	.popup .popup-shadow{
		position: absolute;
		width:100%;
		height:100%;
		top:0;
		left:0;
		background: rgba(0, 0, 0, 0.7);
		z-index: 19;
	}
	.popup .popup-password-input{
		
		position: fixed;
		width:100%;
		left:0;
		bottom:0;
		z-index:100;
		background: #fff;
		
	}
	.popup .popup-password-input .popup-password-input-title{
		font-size: 16px;
		text-align: center;
		position: relative;
		height:55px;
		line-height:55px;
		border-bottom:1PX solid #EBEBEB;
		background: #F6F6FA;
	}
	.popup .popup-password-input .popup-password-input-title .van-icon{
		position: absolute;
		left:15px;
		top:50%;
		transform: translateY(-50%);
		font-size:18px;
	}
	.popup .popup-password-input .van-password-input{
		margin:0;
		padding:30px 15px 45px;
		background: #F6F6FA;
	}
	.popup .popup-password-input .van-password-input [class*=van-hairline]::after{
		border-color:#FF8E20;
	}
	.popup .popup-password-input .van-password-input .van-password-input__security::after{
		border-radius: 10px;
	}
	.popup .popup-password-input .van-password-input .van-hairline--surround::after{
		border-width: 1PX;
	}
	.popup .popup-password-input .van-number-keyboard{
		position: relative;
	}

</style>


